import React, { useState, useEffect } from "react";
import ProjectTable from "../components/ProjectTable";
import axios from "axios";

const ProjectPage = ({ match }) => {
  const [project, setProject] = useState({});

  useEffect(() => {
    const getProjects = async () => {
      const response = await axios("/project.json");
      const res_json = await response.data;

      const res_project = res_json.filter(
        (project) => project.name === match.params.id
      )[0];

      setProject(res_project);
    };
    getProjects();
  }, [match.params, match.params.id]);

  return (
    <section className="container section container-project">
      <div className="u-padding-right">
        <h1 className="title">{project.name}</h1>
        <p className="u-margin-txt">{project.description}</p>
        <ProjectTable project={project} />
      </div>
      <figure className="image">
        {project.imgs ? (
          <img src={project.imgs["img-landing"]} alt="Foto Proyecto" />
        ) : null}
      </figure>
      <div className="u-padding-right">
        <h2 className="title">Propósitos y Metas del Proyecto</h2>
        <p className="u-margin-right">{project.purpose}</p>
      </div>
      <figure className="image">
        {project.imgs ? (
          <img src={project.imgs["img"]} alt="Foto Proyecto" />
        ) : null}
      </figure>
    </section>
  );
};

export default ProjectPage;
