import React from "react";
import { useEffect } from "react";
import { useState } from "react";

const ProjectTable = ({ project }) => {
  const [table, setTable] = useState({});
  useEffect(() => {
    setTable(project.table);
  }, [project]);

  return (
    <table>
      <thead>
        <tr>
          <th>Stack</th>
          <th>Code</th>
          <th>Live</th>
        </tr>
      </thead>
      <tbody>
        {table
          ? table.stack
            ? table.stack.map((tech, index) => (
                <tr key={index}>
                  <td>{tech}</td>
                  <td>
                    {index === 0 ? (
                      <a
                        href={table.repository}
                        target="_blank"
                        rel="noopener noreferrer"
                      >
                        Repositorio
                      </a>
                    ) : (
                      ""
                    )}
                  </td>
                  <td>
                    {index === 0 ? (
                      <a
                        href={table.live}
                        target="_blank"
                        rel="noopener noreferrer"
                      >
                        View Site
                      </a>
                    ) : (
                      ""
                    )}
                  </td>
                </tr>
              ))
            : null
          : null}
      </tbody>
    </table>
  );
};

export default ProjectTable;
