import React, { useEffect, useState } from "react";
import Project from "./Project";

import axios from "axios";

const Projects = () => {
  const [projects, setProjects] = useState([]);

  useEffect(() => {
    const getProjects = async () => {
      const response = await axios("/project.json");
      const res_json = await response.data;

      setProjects(res_json);
    };
    getProjects();
  }, []);

  return (
    <section className="container section">
      <div className="u-padding-right">
        <h2 className="title" data-aos="fade-right" data-aos-duration="1000">
          Proyectos realizados
        </h2>
        <p className="u-margin-txt" data-aos="fade-up" data-aos-duration="1000">
          It is a long established fact that a reader will be distracted by the
          readable content of a page when looking at its layout. The point of
          using Lorem Ipsum is that it has a more-or-less normal distribution
        </p>
      </div>
      <div className="columns is-multiline">
        {projects.map((project) => (
          <Project key={project._id} project={project} />
        ))}
      </div>
    </section>
  );
};

export default Projects;
