import React from "react";

import { Link } from "react-router-dom";

const Navbar = () => {
  const toggleMenu = () => {
    let elements = document.getElementsByClassName("handle-menu");
    for (let el of elements) {
      el.classList.toggle("is-active");
    }
  };
  const handleMenu = (e) => {
    e.preventDefault();
    toggleMenu();
  };

  return (
    <nav
      className="container navbar is-transparent"
      role="navigation"
      aria-label="main navigation"
    >
      <div className="navbar-brand">
        <a className="navbar-item" href="/">
          <img src="../img/logo.svg" className="App-logo" alt="logo" />
        </a>
        <a
          href="/"
          role="button"
          className="navbar-burger handle-menu"
          aria-label="menu"
          aria-expanded="false"
          onClick={handleMenu}
        >
          <span aria-hidden="true"></span>
          <span aria-hidden="true"></span>
          <span aria-hidden="true"></span>
        </a>
      </div>
      <div className="navbar-menu container-menu handle-menu">
        <div className="navbar-end">
          <Link to="/">
            <p className="navbar-item" href="/" onClick={toggleMenu}>
              Inicio
            </p>
          </Link>
          <Link to="/about">
            <p className="navbar-item" href="/" onClick={toggleMenu}>
              Conóceme
            </p>
          </Link>
          <Link to="/projects">
            <p className="navbar-item" href="/" onClick={toggleMenu}>
              Proyectos
            </p>
          </Link>
          <Link to="/contact">
            <p className="navbar-item" href="/" onClick={toggleMenu}>
              Contacto
            </p>
          </Link>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
